export default function renderLocationsCards(location) {
  $.each(location, function (key, value) {
    const content = $(`<li id=${value.id} class='content-item location'>`);

    content.append(`
        <p class=''>Name ${value.name}</p>
        <p class=''>Type ${value.type}</p>
        <p class=''>Dimension ${value.dimension}r</p>
    `);

    $('.content-list').append(content);
  });
}
